:root {
  --color-bg: #ffffff;
  --color-fg: #0f0f0f;
  --color-accent: #2563eb;
  --color-accent-hover: #1d4ed8;
  --color-muted: #6b7280;
  --color-border: #e5e7eb;
  --color-surface: #f9fafb;
  --color-surface-hover: #f3f4f6;
  --font-sans: var(--font-inter), system-ui, sans-serif;
  --font-display: var(--font-space-grotesk), sans-serif;
  --space-section: clamp(4rem, 10vw, 8rem);
  --space-container: clamp(1rem, 5vw, 4rem);
  --ease-out-expo: cubic-bezier(0.16, 1, 0.3, 1);
  --ease-out-quart: cubic-bezier(0.25, 1, 0.5, 1);
  --cursor-x: 50%;
  --cursor-y: 50%;
}
* {
  box-sizing: border-box;
}
html {
  scroll-behavior: auto;
}
body {
  background: var(--color-bg);
  color: var(--color-fg);
  font-family: var(--font-sans);
  line-height: 1.6;
  overflow-x: hidden;
}
html.lenis,
html.lenis body {
  height: auto;
}
.lenis.lenis-smooth {
  scroll-behavior: auto !important;
}
.lenis.lenis-smooth [data-lenis-prevent] {
  overscroll-behavior: contain;
}
.lenis.lenis-stopped {
  overflow: hidden;
}
.lenis.lenis-smooth iframe {
  pointer-events: none;
}
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
::-webkit-scrollbar-track {
  background: transparent;
}
::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.15);
  border-radius: 100px;
  border: 2px solid transparent;
  background-clip: content-box;
}
::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, 0.25);
  background-clip: content-box;
}
[data-lenis-prevent]::-webkit-scrollbar {
  width: 6px;
}
[data-lenis-prevent]::-webkit-scrollbar-track {
  background: transparent;
}
[data-lenis-prevent]::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.1);
  border-radius: 100px;
}
[data-lenis-prevent]::-webkit-scrollbar-thumb:hover {
  background: rgba(0, 0, 0, 0.2);
}
* {
  scrollbar-width: thin;
  scrollbar-color: rgba(0, 0, 0, 0.15) transparent;
}
[data-lenis-prevent] {
  scrollbar-width: thin;
  scrollbar-color: rgba(0, 0, 0, 0.1) transparent;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-display);
  font-weight: 600;
  line-height: 1.2;
}
.container {
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  padding-left: var(--space-container);
  padding-right: var(--space-container);
}
.section {
  padding-top: var(--space-section);
  padding-bottom: var(--space-section);
}
::selection {
  background-color: var(--color-accent);
  color: white;
}
:focus-visible {
  outline: 2px solid var(--color-accent);
  outline-offset: 2px;
}
@keyframes scroll-left {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-50%);
  }
}
.carousel-track {
  animation: scroll-left 25s linear infinite;
}
.carousel-track:hover {
  animation-play-state: paused;
}
@keyframes marquee {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-50%);
  }
}
.marquee-track {
  animation: marquee 20s linear infinite;
}
@keyframes gradient-shift {
  0%,
  100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}
.gradient-text {
  background: linear-gradient(90deg,
      var(--color-accent),
      #8b5cf6,
      #ec4899,
      var(--color-accent));
  background-size: 200% auto;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: gradient-shift 4s ease infinite;
}
@keyframes float {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}
.float-animation {
  animation: float 3s ease-in-out infinite;
}
.hero-gradient {
  background: radial-gradient(ellipse 80% 50% at 50% -20%, rgba(37, 99, 235, 0.15), transparent),
    radial-gradient(ellipse 60% 40% at 80% 50%, rgba(139, 92, 246, 0.1), transparent),
    radial-gradient(ellipse 60% 40% at 20% 80%, rgba(236, 72, 153, 0.08), transparent);
}
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
  .carousel-track,
  .marquee-track {
    animation: none;
  }
}
